/**
 * 
 */
import { fetch, apiFormat } from '@/service/baseService';

const pageListApi = '/api/plat/cardManagement/queryPageableData/{page}/{limit}';          //               method 'get'
const addApi = '/api/plat/cardManagement/create';                          //                   method 'post'
const modifyApi = '/api/plat/cardManagement/modify';                    //                   method 'post'
const deleteApi = '/api/plat/cardManagement/del';                       //                   method 'delete'
const codeSelectListApi = '/api/plat/cardManagement/queryPageableData/1/100';          //               method 'get'

// 
export const findOperatePageList = (opt, para) => fetch.post(apiFormat(pageListApi, opt));

// 
export const addFun = (payload) => fetch.post(addApi, payload);

// 
export const modifyFun = (payload) => fetch.post(modifyApi, payload);

// 
export const deleteFun = (para) => fetch.delete(deleteApi, {params: para});

// 
export const codeSelectList = (para) => fetch.post(codeSelectListApi);



