/**
 * 
 */
import { fetch, apiFormat } from '@/service/baseService';

const pageListApi = '/api/plat/referrer/queryPageableData/{page}/{limit}';          //               method 'get'
const addApi = '/api/plat/referrer/create';                          //                   method 'post'
const modifyApi = '/api/plat/referrer/modify';                    //                   method 'post'
const deleteApi = '/api/plat/referrer/del';                       //                   method 'delete'
const referrerSelectListApi = '/api/plat/referrer/queryPageableData/1/100';          //               method 'get'

// 
export const findOperatePageList = (opt, para) => fetch.post(apiFormat(pageListApi, opt), {params: para});

// 
export const addFun = (payload) => fetch.post(addApi, payload);

// 
export const modifyFun = (payload) => fetch.post(modifyApi, payload);

// 
export const deleteFun = (para) => fetch.delete(deleteApi, {params: para});

// 
export const referrerSelectList = () => fetch.post(referrerSelectListApi);



